<template>
  <el-row>
    <el-table
        v-loading="loading"
        :data="tableData"
        style="width: 100%"
        :cell-style="{height:'.1vh',padding:'1px'}"
        :row-style="{height:'.1vh'}"
        :header-cell-style="{height:'.1vh',padding:'1px'}"
        :border="true"
    >
      <el-table-column
          prop="time"
          label="时间"
          align="center"
      >
      </el-table-column>

      <el-table-column
          prop="mobile__name"
          label="设备名称"
          align="center"
      >
      </el-table-column>

      <el-table-column
          prop="monitor__name"
          label="监测人"
          align="center"
      >
      </el-table-column>

      <el-table-column
          prop="version"
          label="版本号"
          align="center"
      >
      </el-table-column>


    </el-table>

    <Pagination
        style="margin-top: 1vh"
        :total="total"
        @currentChange="currentChange"
    />

  </el-row>
</template>

<script>
// ---------------- axios -----------------
import {getUseRecord, deleteFreqCategory} from "../../../../../api/work"
// --------------- element-ui -------------
import Pagination from "../../../../../components/element-ui/Pagination";
// ----------------- 组件 -------------------

export default {
  components: {
    Pagination
  },
  methods: {
    // --------------- axios --------------------

    /**
     * 获取频点分类
     */
    getFreqList: function () {
      const pd = {
        page: this.page,
        limit: this.limit,
      }
      this.loading = true;
      getUseRecord(pd)
          .then(res => {
            this.loading = false;
            this.updateTableData(res.data.data);
            this.total = res.data.count;
          })
          .catch(err => {
            console.error(err)
          })
    },

    /**
     * 查询频点分类
     * */
    selectFreqList: function (selectForm) {
      const pd = {
        page: this.page,
        limit: this.limit,
        msg: selectForm
      }
      this.loading = true;
      getUseRecord(pd)
          .then(res => {
            this.loading = false;
            this.updateTableData(res.data.data);
            this.total = res.data.count;
          })
          .catch(err => {
            this.loading = false;
            console.error(err)
          })
    },

    /**
     * 删除频点分类
     * */
    deleteMobile: function () {
      const pd = {
        id: this.currMobileId
      }
      deleteFreqCategory(pd)
          .then(res => {
            this.$message(res.data.data.msg);
          })
          .catch(err => {
            console.error(err)
          })
    },

    //  -------------- 更新表格数据 ------------------

    /**
     * 更新表格数据
     * @param dataContent
     */
    updateTableData: function (dataContent) {
      this.tableData = dataContent
    },

    //  --------------- 功能 ------------------

    /**
     * 编辑行
     * @param row
     */
    editRow: function (row) {
      this.editForm = row;
      this.isEditVisiable = true;
    },

    /**
     * 翻页
     * @param val
     */
    currentChange: function (val) {
      this.page = val.currentPage;
      this.limit = val.pageSize;
      this.selectFreqList(this.selectForm);
    },

    /**
     * 修改成功
     */
    editSucceed: function () {
      this.getFreqList();
      this.isEditVisiable = false;
      this.editFlag = !this.editFlag;
    },

    /**
     * 查看工作路径
     * @param row
     */
    workPath: function (row) {
      this.currMobile = row.mobile;
      this.currMobileName = row.name;
      this.workPathVisiable = true;
    },

    /**
     * 删除行
     * @param row
     */
    deleteRow: function (row) {
      this.currMobileName = row.name;
      this.currMobileId = row.id;
      this.deleteMobileVisiable = true;
    },

    /**
     * 收测记录
     * @param row
     */
    textRecord: function (row) {
      this.currMobile = row.mobile;
      this.testRecordVisiable = true;
    },

    /**
     * 加载工作路径
     * @param val
     */
    getWorkPath: function (val) {
      const pd = {
        s_time: val[0],
        e_time: val[1],
        mobile: this.currMobile
      }
      this.getMobileWorkPath(pd);
    },

    /**
     * 确认删除手机
     */
    sureToDeleteMobile: function () {
      this.deleteMobile();
      this.getFreqList();
      this.deleteMobileVisiable = false;
    }
  },
  data() {
    return {
      tableData: [], // 表格数据
      loading: false, // 加载
      // -------------- 弹窗 ---------------
      isEditVisiable: false,
      workPathVisiable: false,
      deleteMobileVisiable: false,
      testRecordVisiable: false,
      // -------------- 分页相关 -------------------
      total: 100,
      page: 1,
      limit: 10,
      //  ---------------- 数据相关 ---------------
      editForm: {},
      currMobile: "",
      currMobileName: "",
      currMobileId: undefined
    }
  },
  created() {
    this.getFreqList();
  },
  props: {
    selectForm: {
      type: Object,
      default: () => {
        return {}
      }
    },
    districtOptions: {
      type: Array,
      default: () => {
        return [
          {
            value: 1,
            label: "未知"
          },
          {
            value: 2,
            label: "假药"
          }, {
            value: 3,
            label: "虚假信息"
          },
          {
            value: 4,
            label: "政治反动"
          }, {
            value: 5,
            label: "恐怖主义"
          },
          {
            value: 6,
            label: "淫秽色情"
          }
        ]
      }
    },
    flushTable: {
      type: Boolean,
      default: false
    },
    freqCategoryOptions: {
      type: Array,
      default: () => {
        return []
      }
    },
  },
  watch: {
    selectForm: function (val) {
      this.selectFreqList(val);
    },
    flushTable: function () {
      this.getFreqList();
    },
  }
}
</script>

