<template>
  <el-form
      label-position="left"
  >

    <!--  所属区域  -->
    <el-form-item label="所属区域">
      <Select
          @SelectChange="districtChange"
          :flushFlag="districtResetFlag"
          :options="districtOptions"
          :selectValue="districtOptions[0].value"
      />
    </el-form-item>

    <!--  手机名称  -->
    <el-form-item label="手机名称">
      <el-input
          size="mini"
          style="width: 12vw"
          v-model="mobileName"
          placeholder="请输入手机名称"></el-input>
    </el-form-item>

    <!--  手机号码  -->
    <el-form-item label="手机号码">
      <el-input
          size="mini"
          style="width: 12vw"
          v-model="phoneNumber"
          placeholder="请输入手机号码"></el-input>
    </el-form-item>

    <!--  系统id  -->
    <el-form-item label="系统id">
      <el-input
          size="mini"
          v-model="mobile"
          style="width: 12vw"
          placeholder="请输入联系方式"></el-input>
    </el-form-item>

    <el-row style="text-align: center">
      <el-button
          type="primary"
          @click="addMobile"
      >提交
      </el-button>
      <el-button
          type="info"
          @click="resetForm"
      >重置
      </el-button>
    </el-row>
  </el-form>
</template>

<script>
// --------------- element-ui ---------------------
import Select from "../../../../../components/element-ui/elSelect"
// --------------- axios ------------------------
import {addMobile} from "../../../../../api/work";

export default {
  components: {Select},
  props: {
    districtOptions: {
      type: Array,
      default: () => {
        return [
          {
            value: 1,
            label: "未知"
          },
          {
            value: 2,
            label: "假药"
          }, {
            value: 3,
            label: "虚假信息"
          },
          {
            value: 4,
            label: "政治反动"
          }, {
            value: 5,
            label: "恐怖主义"
          },
          {
            value: 6,
            label: "淫秽色情"
          }
        ]
      }
    }
  },
  watch:{
    districtOptions:function (val) {
      console.log(val)
    }
  },
  data() {
    return {
      // ----------- 表单 ----------------
      districtNum: 1,
      mobileName: "",
      phoneNumber: "",
      mobile: "",
      districtResetFlag: false
    }
  },
  methods: {
    // ----------------- axios ---------------------

    /**
     * 添加新手机
     */
    addMobile: function () {
      let pd = {
        district: this.districtNum,
        name: this.mobileName,
        phonenumber: this.phoneNumber,
        mobile: this.mobile,
        time: this.nowTime
      }
      addMobile(pd)
          .then(res => {
            this.$message(res.data.data.msg);
            this.$emit("addSuccess");
          })
          .catch(err => {
            console.error(err)
          });
    },
    // ----------------- function ---------------------

    /**
     * 重置表单内容
     */
    resetForm: function () {
      this.districtNum = 1;
      this.mobileName = "";
      this.phoneNumber = "";
      this.mobile = "";
      this.districtResetFlag = !this.districtResetFlag;
    },

    /**
     * 下拉框内容发生变化
     * @param val
     */
    districtChange: function (val) {
      this.districtNum = val;
    },
  },
  computed: {
    nowTime: function () {
      let now = new Date();
      return `${now.getFullYear()}-${now.getMonth() + 1}-${now.getDate()} ${now.getHours()}:${now.getMinutes()}:${now.getSeconds()}`
    }
  }
}
</script>

