<template>
  <el-row>
    <MobileManage
        @getOptionSuccess="getOptionSuccess"
        v-show="currTabNum===1"/>
    <FreqType
        :districtOptions="districtOptions"
        :freqCategoryOptions="freqCategoryOptions"
        v-show="currTabNum===2"/>
    <MonitorManage
        :districtOptions="districtOptions"
        :taizhanOptions="taizhanOptions"
        v-show="currTabNum===3"/>
    <UseRecord
        v-show="currTabNum===4"
        :mobileOptions="mobileOptions"
        :monitorOptions="monitorOptions"
    />
  </el-row>
</template>

<script>
// -------------- 自定义组件 ---------------
import FreqType from "./freqType"
import MobileManage from "./mobileManage"
import MonitorManage from "./monitorManage"
import UseRecord from "./useRecord"

export default {
  components: {
    FreqType, MobileManage, MonitorManage, UseRecord
  },
  props: {
    currTabNum: {
      type: Number,
      default: 1
    },
  },
  data() {
    return {
      districtOptions: [],
      freqCategoryOptions: [],
      mobileOptions: [],
      monitorOptions: [],
      taizhanOptions: []
    }
  },
  methods: {
    /**
     * 下拉框选项
     * @param val
     */
    getOptionSuccess: function (val) {
      this.districtOptions = val.district;
      this.freqCategoryOptions = val.freqCategory;
      this.freqCategoryOptions.unshift({value: 0, label: "全部"})
      this.mobileOptions = val.mobile;
      this.monitorOptions = val.monitor;
      this.taizhanOptions = val.taizhan;
    }
  }
}
</script>

