<template>
  <el-row>
    <el-table
        v-loading="loading"
        :data="tableData"
        style="width: 100%"
        :cell-style="{height:'.1vh',padding:'1px'}"
        :row-style="{height:'.1vh'}"
        :header-cell-style="{height:'.1vh',padding:'1px'}"
        :border="true"
    >

      <el-table-column
          fixed
          prop="name"
          label="移动端名称"
          width="160"
          align="center"
      >
      </el-table-column>

      <el-table-column
          prop="phonenumber"
          label="手机号"
          width="120"
          align="center"
      >
      </el-table-column>

      <el-table-column
          prop="district"
          label="所属地区"
          width="180"
          align="center"
      >
      </el-table-column>

      <el-table-column
          prop="time"
          label="加入时间"
          width="200"
          align="center"
      >
      </el-table-column>

      <el-table-column
          label="工作路径"
          width="120"
          :resizable="true"
          :show-overflow-tooltip="true"
          align="center"
      >
        <template slot-scope="scope">
          <el-button
              size="mini"
              @click="workPath(scope.row)"
              type="primary">工作路劲
          </el-button>
        </template>
      </el-table-column>

      <el-table-column
          label="操作"
          align="center"
      >
        <template slot-scope="scope">
          <el-button
              size="mini"
              type="danger"
              @click="deleteRow(scope.row)">删除
          </el-button>
          <el-button
              size="mini"
              @click="editRow(scope.row)"
              type="info">修改
          </el-button>
        </template>
      </el-table-column>

      <el-table-column
          label="收测记录"
          width="120"
          align="center"
      >
        <template slot-scope="scope">
          <el-button
              size="mini"
              @click="textRecord(scope.row)"
              type="primary">收测记录
          </el-button>
        </template>
      </el-table-column>

    </el-table>

    <Pagination
        style="margin-top: 1vh"
        :total="total"
        @currentChange="currentChange"
    />

    <!--  弹窗  -->

    <!--  手机信息修改  -->
    <Dialog
        :visiable="isEditVisiable"
        :info="{title:'手机信息修改',width:'25%'}"
        :top="'5%'"
        @close="isEditVisiable=false"
    >
      <EditForm
          :districtOptions="districtOptions"
          :info="editForm"
          @editSucceed="editSucceed"
      />
    </Dialog>

    <!-- 工作路劲 -->
    <Dialog
        :visiable="workPathVisiable"
        @close="workPathVisiable=false"
        :info="{title:'工作路劲',width:'45%'}"
        :top="'5%'"
    >
      <WorkPathForm
          @workPath="getWorkPath"
      />
    </Dialog>

    <!-- 删除手机 -->
    <Dialog
        :visiable="deleteMobileVisiable"
        @close="deleteMobileVisiable=false"
        :info="{title:'删除手机',width:'25%'}"
        :top="'5%'"
    >
      <el-row
          style="text-align: center"
      >
        <span>确定删除"{{ currMobileName }}"么</span>
      </el-row>

      <el-row
          style="
          text-align: center;
          margin-top: 2vh"
      >
        <el-button
            @click="sureToDeleteMobile"
            type="primary"
            size="mini">确定
        </el-button>
        <el-button
            @click="deleteMobileVisiable=false"
            type="info"
            size="mini">取消
        </el-button>
      </el-row>
    </Dialog>

    <!-- 收测记录 -->
    <Dialog
        :visiable="testRecordVisiable"
        @close="testRecordVisiable=false"
        :info="{title:'收测记录',width:'25%'}"
        :top="'5%'"
    >
      <RedioTest
          :mobile="currMobile"/>
    </Dialog>

  </el-row>
</template>

<script>
// ---------------- axios -----------------
import {getMobileList, deleteMobile, getMobileWorkPath} from "../../../../../api/work"
// --------------- element-ui -------------
import Pagination from "../../../../../components/element-ui/Pagination";
import Dialog from "../../../../../components/element-ui/dialog"
// ----------------- 组件 -------------------
import EditForm from "./editForm"
import WorkPathForm from "./workPathForm"
import RedioTest from "./redioTest"

export default {
  components: {
    Pagination, Dialog, EditForm, WorkPathForm, RedioTest
  },
  methods: {
    // --------------- axios --------------------

    /**
     * 获取手机信息
     */
    getMobileList: function () {
      const pd = {
        page: this.page,
        limit: this.limit,
      }
      this.loading = true;
      getMobileList(pd)
          .then(res => {
            this.loading = false;
            this.updateTableData(res.data.data);
            this.total = res.data.count;
            this.needSelect = false;
          })
          .catch(err => {
            this.loading = false;
            console.error(err)
          })
    },

    /**
     * 查询手机信息
     * */
    selectMobileList: function (selectForm) {
      // selectForm.mobile = String(selectForm.mobile);
      // selectForm.category = String(selectForm.category);
      const pd = {
        page: this.page,
        limit: this.limit,
        0: selectForm
      }
      this.loading = true;
      getMobileList(pd)
          .then(res => {
            this.loading = false;
            this.updateTableData(res.data.data);
            this.total = res.data.count;
            this.needSelect = true;
          })
          .catch(err => {
            this.loading = false;
            console.error(err)
          })
    },

    /**
     * 删除手机
     * */
    deleteMobile: function () {
      const pd = {
        id: this.currMobileId
      }
      deleteMobile(pd)
          .then(res => {
            this.$message(res.data.data.msg);
          })
          .catch(err => {
            console.error(err)
          })
    },

    /**
     * 获取手机工作路径
     * */
    getMobileWorkPath: function (pd) {
      getMobileWorkPath(pd)
          .then(res => {
            if (res.data.data.length === 0) {
              this.$message.info("无路径数据")
            } else {
              this.$message.success(res.data.msg);
              const params = {
                path: res.data.data,
                name: this.currMobileName
              }
              this.$emit("getWorkPath", params);
            }
            this.workPathVisiable = false;
          })
          .catch(err => {
            this.workPathVisiable = false;
            this.$message.error("请求失败")
            console.error(err)
          });
    },
    //  -------------- 更新表格数据 ------------------

    /**
     * 更新表格数据
     * @param dataContent
     */
    updateTableData: function (dataContent) {
      this.tableData = dataContent
    },

    //  --------------- 功能 ------------------

    /**
     * 编辑行
     * @param row
     */
    editRow: function (row) {
      this.editForm = row;
      this.isEditVisiable = true;
    },

    /**
     * 翻页
     * @param val
     */
    currentChange: function (val) {
      this.page = val.currentPage;
      this.limit = val.pageSize;
      if (this.needSelect) {
        this.selectMobileList(this.selectForm);
      } else {
        this.getMobileList()
      }
    },

    /**
     * 修改成功
     */
    editSucceed: function () {
      this.getMobileList();
      this.isEditVisiable = false;
      this.editFlag = !this.editFlag;
    },

    /**
     * 查看工作路径
     * @param row
     */
    workPath: function (row) {
      this.currMobile = row.mobile;
      this.currMobileName = row.name;
      this.workPathVisiable = true;
    },

    /**
     * 删除行
     * @param row
     */
    deleteRow: function (row) {
      this.currMobileName = row.name;
      this.currMobileId = row.id;
      this.deleteMobileVisiable = true;
    },

    /**
     * 收测记录
     * @param row
     */
    textRecord: function (row) {
      this.currMobile = row.mobile;
      this.testRecordVisiable = true;
    },

    /**
     * 加载工作路径
     * @param val
     */
    getWorkPath: function (val) {
      const pd = {
        s_time: val[0],
        e_time: val[1],
        mobile: this.currMobile
      }
      this.getMobileWorkPath(pd);
    },

    /**
     * 确认删除手机
     */
    sureToDeleteMobile: function () {
      this.deleteMobile();
      this.getMobileList();
      this.deleteMobileVisiable = false;
    }
  },
  data() {
    return {
      tableData: [], // 表格数据
      loading: false, // 加载
      // -------------- 弹窗 ---------------
      isEditVisiable: false,
      workPathVisiable: false,
      deleteMobileVisiable: false,
      testRecordVisiable: false,
      // -------------- 分页相关 -------------------
      total: 100,
      page: 1,
      limit: 10,
      //  ---------------- 数据相关 ---------------
      editForm: {},
      currMobile: "",
      currMobileName: "",
      currMobileId: undefined,
      needSelect: false
    }
  },
  created() {
    this.getMobileList();
  },
  props: {
    selectForm: {
      type: Object,
      default: () => {
        return {}
      }
    },
    districtOptions: {
      type: Array,
      default: () => {
        return [
          {
            value: 1,
            label: "未知"
          },
          {
            value: 2,
            label: "假药"
          }, {
            value: 3,
            label: "虚假信息"
          },
          {
            value: 4,
            label: "政治反动"
          }, {
            value: 5,
            label: "恐怖主义"
          },
          {
            value: 6,
            label: "淫秽色情"
          }
        ]
      }
    },
    flushTable: {
      type: Boolean,
      default: false
    },
  },
  watch: {
    selectForm: function (val) {
      this.page = 1;
      this.selectMobileList(val);
    },
    flushTable: function () {
      this.getMobileList();
    },
  }
}
</script>

