<template>
  <el-row>

    <!--  查询条件与地图  -->
    <el-row :gutter="15">

      <Elcard style="height: 15vh">
        <div slot="header">
          <span>查询条件</span>
          <el-button
              @click="resettingSelectOption"
              style="float: right"
              type="primary"
              size="mini">
            重置查询条件
          </el-button>
        </div>

        <SelectForm
            :districtOptions="mobileOptions"
            :freqCategoryOptions="monitorOptions"
            @resetSelectForm="resettingTable"
            @selectChange="selectChange"
            @getDistrictOptionSuccess="getDistrictOptionSuccess"
            :Reset="resettingFlag"
        />

      </Elcard>
    </el-row>

    <!--  表格  -->
    <el-row
        style="margin-top: 1vh">

      <ElCard style="height: auto">
        <div slot="header">
          <span>使用记录</span>
        </div>
        <Table
            :selectForm="selectForm"
            :flushTable="flushTable"
            @getWorkPath="getWorkPath"
        />
      </ElCard>
    </el-row>

    <!---------------------  弹窗  ------------------>


  </el-row>
</template>

<script>
// ---------------- element-ui ------------------
import Elcard from "../../../../components/element-ui/elCard"
// --------------- 自定义组件 ------------------
import SelectForm from "./useRecord-cpt/selectForm"
import Table from "./useRecord-cpt/table"

export default {
  components: {
    Elcard, SelectForm, Table,
  },
  methods: {
    // ---------------- 功能 -------------------
    /**
     * 重置查询表单
     * */
    resettingSelectOption: function () {
      this.resettingFlag = !this.resettingFlag;
    },

    /**
     * 添加新手机
     */
    addMobile: function () {
      this.addMobileVisiable = true;
    },

    /**
     * 获取所属地区成功
     * @param val
     */
    getDistrictOptionSuccess: function (val) {
      this.districtOption = val;
    },

    /**
     * 添加手机成功,刷新表格
     */
    addSuccess: function () {
      this.flushTable = !this.flushTable;
      this.addMobileVisiable = false;
    },

    /**
     * 获取工作路径
     * @param val
     * @returns {number}
     */
    getWorkPath: function (val) {
      this.workPath = val.path;
      this.currMobileName = val.name;
    },

    /**
     * 选择地区成功
     * @param val
     */
    selectChange: function (val) {
      if (val.type === 0) {
        val.type = undefined;
      }
      this.selectForm = val;
    },

    /**
     * 重置查询项 刷新表格
     */
    resettingTable: function () {
      this.flushTable = !this.flushTable;
    }
  },
  data() {
    return {
      showSatelite: true,
      showSateliteDialog: true,
      // ----------- 弹窗 -------------
      isMapVisiable: false,
      addMobileVisiable: false,
      // ---------- 查询表单 ------------
      resettingFlag: false,
      flushTable: false,
      selectForm: {},
      //   ----------- 功能 ------------
      workPath: [],
      currMobileName: undefined
    }
  },
  props: {
    mobileOptions: {
      type: Array,
      default: () => {
        return []
      }
    },
    monitorOptions: {
      type: Array,
      default: () => {
        return []
      }
    },
  },
}
</script>

