<template>
  <el-form
      label-position="left"
  >

    <!--  所属区域  -->
    <el-form-item label="所属区域">
      <Select
          @SelectChange="districtChange"
          :flushFlag="districtResetFlag"
          :options="districtOptions"
          :selectValue="district"
      />
    </el-form-item>

    <!--  姓名  -->
    <el-form-item label="姓名">
      <el-input
          size="mini"
          v-model="name"
          style="width: 12vw"
          placeholder="请输入监测人员姓名"></el-input>
    </el-form-item>

    <!--  提交与重置  -->
    <el-row style="text-align: center">
      <el-button
          type="primary"
          @click="editFreqCategory"
      >提交
      </el-button>
      <el-button
          type="primary"
          @click="resetForm"
      >重置
      </el-button>
    </el-row>

  </el-form>
</template>

<script>
// --------------- element-ui ---------------------
import Select from "../../../../../components/element-ui/elSelect"
// --------------- axios ------------------------
import {editMonitor} from "../../../../../api/work";

export default {
  components: {Select},
  props: {
    info: {
      type: Object,
      default: () => {
        return {
          district_num: 1,
          name: "",
          phonenumber: "",
          id: ""
        }
      }
    },
    districtOptions: {
      type: Array,
      default: () => {
        return [
          {
            value: 1,
            label: "未知"
          },
          {
            value: 2,
            label: "假药"
          }, {
            value: 3,
            label: "虚假信息"
          },
          {
            value: 4,
            label: "政治反动"
          }, {
            value: 5,
            label: "恐怖主义"
          },
          {
            value: 6,
            label: "淫秽色情"
          }
        ]
      }
    },
    freqCategoryOptions: {
      type: Array,
      default: () => {
        return []
      }
    },
  },
  created() {
    this.resetForm();
  },
  watch: {
    info: function (val) {
      this.district = val.district_num;
      this.name = val.name;
      this.type = val.type_num;
      this.freq = val.freq;
      this.id = val.id;
    },
  },
  data() {
    return {
      // ----------- 表单 ----------------
      district: 1,
      name: "",
      type: 1,
      freq: undefined,
      districtResetFlag: false,
      id: undefined
    }
  },
  methods: {
    // --------------- axios  ------------
    /**
     * 修改手机信息
     */
    editFreqCategory: function () {
      if (this.name === "") {
        this.$message.error("必须填写名称");
        return 0;
      }
      let pd = {
        district: this.district,
        name: this.name,
        id: this.id,
        time: this.nowTime
      }
      editMonitor(pd)
          .then(res => {
            this.$message(res.data.data.msg);
            this.$emit("editSucceed");
          })
          .catch(err => {
            console.error(err)
          });
    },
    // ------------ 功能 -----------------

    /**
     * 所属地区发生变化
     * @param val
     */
    districtChange: function (val) {
      this.district = val;
    },

    /**
     * 重置
     */
    resetForm: function () {
      this.district = this.info.district_num;
      this.name = this.info.name;
      this.type = this.info.type_num;
      this.districtResetFlag = !this.districtResetFlag;
      this.id = this.info.id;
    },
    /**
     * 频点类型发生变化
     * @param val
     */
    freqTypeChange: function (val) {
      this.type = val
    }
  },
  computed: {
    nowTime: function () {
      let now = new Date();
      return `${now.getFullYear()}-${now.getMonth() + 1}-${now.getDate()} ${now.getHours()}:${now.getMinutes()}:${now.getSeconds()}`
    },
    freqTypeForAdd: function () {
      let fto = JSON.parse(JSON.stringify(this.freqCategoryOptions));
      fto.shift();
      return fto;
    }
  }
}
</script>

